select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #4b3b55;
  background-image: none;
  font-size: 1.3rem;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.SuperSelect_select__3URu3 {
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #4b3b55;
  overflow: hidden;
  border-radius: 0.25em;
  margin: 1rem;
}
select {
  flex: 1 1;
  padding: 0 1rem;
  color: rgb(241, 241, 241);
  cursor: pointer;
}
/* Arrow */
.SuperSelect_select__3URu3::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #4b3b55;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}
/* Transition */
.SuperSelect_select__3URu3:hover::after {
  color: rgb(213, 169, 255);
}

